import "../css/component/Header.scss";
import { Link, useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import useScroll from "../custom_hooks/useScroll.tsx";
import ImgLogo01 from "../assets/img/common/logo_01.png";
import ImgLogo02 from "../assets/img/common/logo_02.png";

const headerMargin = 120;

const HeaderWrapper = styled.header`
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    text-align: center;
    width: 100%;
    height: 100px;
    background: #ffffff00;
    z-index: 1;
`;
const List = styled.ul`
    display: inline-flex;
    width: 100%;
    justify-content: right;
    list-style: none;
    margin-right: ${headerMargin}px;
`;
const Item = styled.li`
    margin-right: 40px;
    text-transform: uppercase;
    color: ${(props) => (props.selected ? "black" : "white")};
`;
const Logo = styled.img`
    display: inline;
    width: 130px;
    margin-left: ${headerMargin}px;
    cursor: pointer;
`;

export default function Header() {
    const { pathname } = useLocation();
    const { y } = useScroll();
    const isFloating: boolean = y > 0;
    const navigate = useNavigate();

    return (
        <HeaderWrapper id="header-pc" className={isFloating ? "float" : ""}>
            <Logo
                src={isFloating ? ImgLogo01 : ImgLogo02}
                onClick={() => {
                    navigate("/");
                }}
            />
            <List>
                <Item selected={pathname.startsWith("/introduction")}>
                    <Link to="/introduction">회사소개</Link>
                </Item>
                <Item selected={pathname.startsWith("/production")}>
                    <Link to="/production">주요사업</Link>
                </Item>
                <Item selected={pathname.startsWith("/service")}>
                    <Link to="/service">고객서비스</Link>
                </Item>
            </List>
        </HeaderWrapper>
    );
}
