import { useNavigate } from "react-router-dom";
import styled from "styled-components";

interface MoreButtonProps {
    link: string;
    style: object;
    children: string;
}

const Span = styled.span`
    display: inline;
    position: absolute;
    text-align: left;
    font-size: 12px;
    border: 1px solid #fff;
    border-radius: 20px;
    padding: 2px 9px;
    cursor: pointer;
`;

export default function MoreButton(prop: MoreButtonProps) {
    const navigate = useNavigate();
    const gotoPage = () => {
        navigate(prop.link || "/");
    };

    return (
        <Span style={{ ...prop.style }} onClick={gotoPage}>
            {prop.children}
        </Span>
    );
}
